/**
 * @desc 
 * @param {} func
 * @param {} wait
 */
const debounce = (func, wait = 50) => {
	// id
	let timer = 0;
	// 
	// 
	// 
	return function (...args) {
		if (timer) clearTimeout(timer)
		timer = setTimeout(() => {
			func.apply(this, args)
		}, wait)
	}
}

export {
	debounce
}